import React, { Component } from 'react';
import Facebook from '../img/facebook.svg';
import Instagram from '../img/instagram.svg';
import Twitter from '../img/twitter.svg';
import Medium from '../img/medium.svg';
import Mailbox from '../img/mailbox.svg';


class Footer extends Component {
  render() {
    return (
      <footer className="footer">
        <div className="container">
          <div className="footer__head">
            <div className="footer__head-text">
              <p className="footer__text">
                MadAppGang is a team of tech experts specializing in mobile technologies.
                We develop applications for iOS, Android, WatchOS, tvOS, Wear OS, wearables,
                and provide loT system integration services. Our experience with mobile apps,
                artificial intelligence, and embedded system provides a uniquely powerful
                resource for our clients.
              </p>
              <p className="footer__text">
                We work with established businesses and startups who are seeking to innovate
                and improve customer experience. Over the past few years we`ve built solutions
                for fintech, healthcare, fitness, transportation, and communication industries.
              </p>
            </div>
            <div className="footer__contacts">
              <p className="footer__title">Follow us</p>
              <ul className="footer__social">
                <li className="footer__social-item">
                  <a href="https://www.facebook.com/madappgang"
                     target="_blank" rel="noopener noreferrer"
                     className="footer__social-link"
                  >
                    <img src={Facebook} alt="Facebook icon" />
                  </a>
                </li>
                <li className="footer__social-item">
                  <a href="https://twitter.com/madappgang"
                     target="_blank" rel="noopener noreferrer"
                     className="footer__social-link"
                  >
                    <img src={Twitter} alt="Twitter icon" />
                  </a>
                </li>
                <li className="footer__social-item">
                  <a href="https://www.instagram.com/madappgang"
                     target="_blank" rel="noopener noreferrer"
                     className="footer__social-link"
                  >
                    <img src={Instagram} alt="instagram icon" />
                  </a>
                </li>
                <li className="footer__social-item">
                  <a href="https://medium.com/@i_49105"
                     target="_blank" rel="noopener noreferrer"
                     className="footer__social-link"
                  >
                    <img src={Medium} alt="medium icon" />
                  </a>
                </li>
              </ul>
              <a href="mailto:i@madappgang.com" className="footer__mail-link">
                <img src={Mailbox} alt="email icon" />
              </a>
            </div>
          </div>
        </div>

        <div className="footer__bottom">
          <div className="footer__wrap">
            <nav className="footer__nav">
              <ul className="nav__list">
                <li className="nav__item">
                  <a href="https://madappgang.com/services" className="nav__link">services</a>
                </li>
                <li className="nav__item">
                  <a href="https://madappgang.com/experience" className="nav__link">experience</a>
                </li>
                <li className="nav__item">
                  <a href="https://madappgang.com/foundation/philosophy" className="nav__link">foundation</a>
                </li>
                <li className="nav__item">
                  <a href="https://madappgang.com/team" className="nav__link">team</a>
                </li>
                <li className="nav__item">
                  <a href="https://madappgang.com/blog" className="nav__link">blog</a>
                </li>
              </ul>
            </nav>

            <p className="footer__copyright">
              © MadAppGang Pty Ltd, 2018
            </p>
          </div>
        </div>
      </footer>
    )
  }
}

export default Footer;