import React from 'react';
import Notebook from '../img/mc_book.svg';
import ContactForm from './ContactForm';


const Body = () => {
  return (
    <section className="main-content">
      <div className="container">
        <div className="main-content__head">
          <h1 className="main-content__title">Get in touch</h1>
          <p className="main-content__subtitle">Let us know<br/> how we can help</p>
        </div>
        <div className="main-content__body">
          <ContactForm />
          <div className="main-content__text-content">
            <h3 className="main-content__text-title">What's next</h3>
            <p className="main-content__text">
              We’ll contact you within a few hours with our next steps.
              We normally schedule a call with our engineers to discuss your project in more detail.
              If you’d like to sign an NDA, please let us know. We’ll prepare it for you.
            </p>
            <p className="main-content__text">
              Since we live on two different continents (Australia and Europe)
              we are available around the clock.
            </p>
          </div>
        </div>
        <div className="main-content__visit">
          <h3 className="main-content__visit-title">Visit us</h3>
          <p className="main-content__visit-text">
            Our company management and business development team is located in Sydney, Australia.
            Our engineering team is located in Kremenchuk, Ukraine.
          </p>
          <img src={Notebook} alt="Notebook" className="main-content__image" />
        </div>
      </div>
    </section>
  ) 
}
  
export default Body;