import React from 'react';


const ContactForm = () => { 
  return (
    <form className="main-content__form" action="mailto:random@mail.com" method="POST" encType="text/plain">
      <input type="text" name ="user-name" className="main-content__name"
        placeholder="Name" required
      />
      <input type="email" className="main-content__mail"
        placeholder="E-mail" name="user-mail" required
      />
      <textarea name="message" className="main-content__message" placeholder="Message"></textarea>
      <input type="submit" className="main-content__submit"
        value="send" 
      />
    </form>
  )
}

export default ContactForm;